<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者:{{blog.author}}</p>
        <p>分类</p>
        <ul>
            <li v-for="(xxx,i) in blog.categories" :key="i">
                {{xxx}}

            </li>
        </ul>
        <button @click="deleteSingleBlog()" id="del">删除</button>
        <router-link :to="'/edit/'+id" id="bianji">编辑</router-link>
    </div>    
</template>

<script>
export default { 
    name:"single-blog",
    data(){
        return{
            id:this.$route.params.id,
            blog:{}
        }
    },
    created(){
      this.$axios.get('/posts/'+this.id+".json")
      .then(function(data){
          return data.data;
      })
        .then(data=>{
            this.blog=data;
        })
    },
    methods:{
        deleteSingleBlog(){
            this.$axios.delete('/posts/'+this.id+".json")
            .then(response=>{
                this.$router.push({path:'/'})
            })
        }
    } 
}
</script>

<style scoped>
    #single-blog{
        max-width: 960px;
        margin:0 auto;
        padding: 20px;
        background: #eee;
        border:1px dotted #aaa;
        max-width: 800px;
    }
</style>


